import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, Play, RotateCcw, Trophy, Settings, X, Lightbulb } from 'lucide-react';
import { Button } from '@/components/Button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/Card';

// --- TYPES ---

type ItemType = 'Fruit' | 'Veggie' | 'Toy' | 'Animal';
type ItemColor = 'Red' | 'Green' | 'Yellow';

interface Item {
  id: number;
  icon: string;
  type: ItemType;
  color: ItemColor;
  name: string;
}

interface Rule {
  id: number;
  property: 'Type' | 'Color' | 'Name';
  operator: 'Is';
  value: string;
  destination: 'Bin A' | 'Bin B';
}

interface Level {
  id: number;
  name: string;
  description: string;
  difficulty: 'Easy' | 'Medium' | 'Hard';
  queue: Item[]; // Items that will appear
  bins: { id: string; label: string; accepts: (item: Item) => boolean }[];
  hint: string;
  xpReward: number;
}

// --- DATA ---

const ITEMS: Record<string, Item> = {
  apple: { id: 1, icon: '', type: 'Fruit', color: 'Red', name: 'Apple' },
  banana: { id: 2, icon: '', type: 'Fruit', color: 'Yellow', name: 'Banana' },
  broccoli: { id: 3, icon: '', type: 'Veggie', color: 'Green', name: 'Broccoli' },
  carrot: { id: 4, icon: '', type: 'Veggie', color: 'Red', name: 'Carrot' },
  ball: { id: 5, icon: '', type: 'Toy', color: 'Yellow' as any, name: 'Ball' }, // Simplified color type for game
  bear: { id: 6, icon: '', type: 'Toy', color: 'Red' as any, name: 'Bear' },
  pepper: { id: 7, icon: '', type: 'Veggie', color: 'Red', name: 'Pepper' },
  lemon: { id: 8, icon: '', type: 'Fruit', color: 'Yellow', name: 'Lemon' },
};

const LEVELS: Level[] = [
  {
    id: 1,
    name: 'Fruit Sorter',
    description: 'Send all Fruits to Bin A. Send everything else to Bin B.',
    difficulty: 'Easy',
    queue: [ITEMS.apple, ITEMS.broccoli, ITEMS.banana, ITEMS.pepper, ITEMS.lemon],
    bins: [
      { id: 'Bin A', label: 'Fruits Only', accepts: (i) => i.type === 'Fruit' },
      { id: 'Bin B', label: 'Others', accepts: (i) => i.type !== 'Fruit' },
    ],
    hint: 'IF Type IS Fruit -> Bin A',
    xpReward: 100,
  },
  {
    id: 2,
    name: 'Red Alert',
    description: 'The factory only wants RED items in Bin A.',
    difficulty: 'Easy',
    queue: [ITEMS.apple, ITEMS.banana, ITEMS.pepper, ITEMS.broccoli, ITEMS.carrot],
    bins: [
      { id: 'Bin A', label: 'Red Items', accepts: (i) => i.color === 'Red' },
      { id: 'Bin B', label: 'Others', accepts: (i) => i.color !== 'Red' },
    ],
    hint: 'IF Color IS Red -> Bin A',
    xpReward: 125,
  },
  {
    id: 3,
    name: 'Specific Sorting',
    description: 'We need Bananas in Bin A. Everything else in Bin B.',
    difficulty: 'Medium',
    queue: [ITEMS.apple, ITEMS.banana, ITEMS.banana, ITEMS.lemon, ITEMS.apple],
    bins: [
      { id: 'Bin A', label: 'Bananas', accepts: (i) => i.name === 'Banana' },
      { id: 'Bin B', label: 'Compost', accepts: (i) => i.name !== 'Banana' },
    ],
    hint: 'IF Name IS Banana -> Bin A',
    xpReward: 150,
  },
  {
    id: 4,
    name: 'Veggie Vault',
    description: 'Sort Veggies to Bin A. Fruits to Bin B. Ignore Toys.',
    difficulty: 'Medium',
    queue: [ITEMS.carrot, ITEMS.apple, ITEMS.broccoli, ITEMS.pepper, ITEMS.banana],
    bins: [
      { id: 'Bin A', label: 'Veggies', accepts: (i) => i.type === 'Veggie' },
      { id: 'Bin B', label: 'Fruits', accepts: (i) => i.type === 'Fruit' },
    ],
    hint: 'Rule 1: IF Type IS Veggie -> Bin A.\nRule 2: IF Type IS Fruit -> Bin B.',
    xpReward: 200,
  },
];

// --- COMPONENT ---

export function EmojiFactory() {
  const navigate = useNavigate();
  
  // Game State
  const [currentLevelId, setCurrentLevelId] = useState<number | null>(null);
  const [gameState, setGameState] = useState<'menu' | 'playing' | 'won'>('menu');
  
  // Factory State
  const [rules, setRules] = useState<Rule[]>([]);
  const [beltItems, setBeltItems] = useState<Item[]>([]);
  const [isRunning, setIsRunning] = useState(false);
  const [simulationSpeed] = useState(1500); // ms per item
  const [logs, setLogs] = useState<string[]>([]);

  const currentLevel = LEVELS.find(l => l.id === currentLevelId) || LEVELS[0];

  // Initialize Level
  const startLevel = (id: number) => {
    setCurrentLevelId(id);
    const level = LEVELS.find(l => l.id === id)!;
    setBeltItems([...level.queue]);
    setRules([{ id: 1, property: 'Type', operator: 'Is', value: 'Fruit', destination: 'Bin A' }]); // Default rule
    setLogs([]);
    setIsRunning(false);
    setGameState('playing');
  };

  // --- LOGIC ENGINE ---

  const evaluateRules = (item: Item): string => {
    // Check user rules in order (Top to Bottom logic)
    for (const rule of rules) {
      let match = false;
      if (rule.property === 'Type' && item.type === rule.value) match = true;
      if (rule.property === 'Color' && item.color === rule.value) match = true;
      if (rule.property === 'Name' && item.name === rule.value) match = true;

      if (match) return rule.destination;
    }
    return 'Bin B'; // Default Fallback if no rules match (Logic "Else")
  };

  const processNextItem = () => {
    if (beltItems.length === 0) {
      finishLevel();
      return;
    }

    const currentItem = beltItems[0];
    const decision = evaluateRules(currentItem);
    
    // Check correctness
    const targetBin = currentLevel.bins.find(b => b.id === decision);
    let isCorrect = false;

    if (targetBin) {
      isCorrect = targetBin.accepts(currentItem);
    }

    // Animation / State Update
    setLogs(prev => [`${currentItem.icon} -> ${decision} (${isCorrect ? 'OK' : 'FAIL'})`, ...prev.slice(0, 4)]);
    
    if (!isCorrect) {
      setIsRunning(false);
      alert(`Oops! The ${currentItem.name} went to ${decision}, but that's wrong! Check your logic.`);
      // Reset level
      setBeltItems([...currentLevel.queue]);
      return;
    }

    // Move next
    setBeltItems(prev => prev.slice(1));

    if (beltItems.length === 1) {
      // Last item processed successfully
      setTimeout(finishLevel, 1000);
    }
  };

  const finishLevel = () => {
    setIsRunning(false);
    setGameState('won');
  };

  // Simulation Loop
  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (isRunning && beltItems.length > 0) {
      timer = setTimeout(processNextItem, simulationSpeed);
    }
    return () => clearTimeout(timer);
  }, [isRunning, beltItems, simulationSpeed]);

  // --- UI HANDLERS ---

  const addRule = () => {
    setRules([...rules, { id: Date.now(), property: 'Type', operator: 'Is', value: 'Fruit', destination: 'Bin A' }]);
  };

  const removeRule = (id: number) => {
    setRules(rules.filter(r => r.id !== id));
  };

  const updateRule = (id: number, field: keyof Rule, value: string) => {
    setRules(rules.map(r => r.id === id ? { ...r, [field]: value } : r));
  };

  // --- RENDERERS ---

  if (gameState === 'menu') {
    return (
      <div className="min-h-screen bg-amber-50 p-6 flex flex-col items-center justify-center font-sans">
        <div className="w-full max-w-5xl">
          <Card className="mb-8 border-amber-200 shadow-sm bg-white">
            <CardHeader className="text-center pb-6 border-b border-amber-100">
              <CardTitle className="text-5xl font-black text-amber-600 flex items-center justify-center gap-4">
                 Emoji Factory
              </CardTitle>
              <p className="text-gray-500 mt-4 text-lg">Master logic by sorting the factory line!</p>
            </CardHeader>
          </Card>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {LEVELS.map((level) => (
              <button
                key={level.id}
                onClick={() => startLevel(level.id)}
                className="group relative overflow-hidden p-6 rounded-2xl bg-white border-2 border-amber-100 hover:border-amber-400 transition-all text-left shadow-sm hover:shadow-xl hover:-translate-y-1"
              >
                <div className="flex justify-between items-start mb-4">
                  <span className="px-3 py-1 rounded-full text-xs font-bold bg-amber-100 text-amber-700 uppercase tracking-wider">
                    {level.difficulty}
                  </span>
                  <Settings className="w-6 h-6 text-gray-300 group-hover:text-amber-500" />
                </div>
                <h3 className="text-2xl font-bold text-gray-800 mb-2">{level.name}</h3>
                <p className="text-sm text-gray-500 mb-4">{level.description}</p>
                <div className="flex gap-2">
                  {level.queue.slice(0, 5).map((i, idx) => (
                    <span key={idx} className="text-xl">{i.icon}</span>
                  ))}
                </div>
              </button>
            ))}
          </div>
          
          <div className="mt-8 text-center">
            <Button onClick={() => navigate('/games')} variant="ghost" className="text-gray-400 hover:text-gray-600">
              <ArrowLeft className="w-4 h-4 mr-2" /> Back to Dashboard
            </Button>
          </div>
        </div>
      </div>
    );
  }

  if (gameState === 'playing') {
    return (
      <div className="min-h-screen bg-slate-50 flex flex-col h-screen overflow-hidden">
        {/* Header */}
        <header className="bg-white border-b border-gray-200 px-6 py-4 flex justify-between items-center shrink-0">
          <div className="flex items-center gap-4">
            <Button size="icon" variant="ghost" onClick={() => setGameState('menu')}>
              <ArrowLeft className="w-5 h-5 text-gray-500" />
            </Button>
            <div>
              <h2 className="text-xl font-bold text-gray-800">{currentLevel.name}</h2>
              <p className="text-xs text-gray-500">Sort the items correctly!</p>
            </div>
          </div>
          <div className="flex items-center gap-4">
             <div className="text-right mr-4">
                <p className="text-xs text-gray-400 font-bold uppercase">Queue</p>
                <p className="text-xl font-bold text-blue-600">{beltItems.length} items</p>
             </div>
             <Button 
                onClick={() => setIsRunning(!isRunning)} 
                className={`min-w-[140px] text-lg ${isRunning ? 'bg-red-500 hover:bg-red-600' : 'bg-green-600 hover:bg-green-700'}`}
             >
                <Play className="w-4 h-4 mr-2 fill-current" />
                {isRunning ? 'Stop' : 'Start'}
             </Button>
          </div>
        </header>

        <div className="flex flex-1 overflow-hidden">
          {/* LEFT: Logic Editor */}
          <div className="w-1/2 min-w-[400px] bg-slate-100 flex flex-col border-r border-slate-200 p-6 overflow-y-auto">
            <div className="mb-6 flex justify-between items-center">
                <h3 className="text-lg font-bold text-slate-700 flex items-center gap-2">
                    <Settings className="w-5 h-5" /> Sorting Rules
                </h3>
                <Button size="sm" onClick={addRule} variant="outline" className="border-dashed border-slate-400">
                    + Add Rule (Else If)
                </Button>
            </div>

            <div className="space-y-3">
                {rules.map((rule, index) => (
                    <Card key={rule.id} className="relative overflow-visible">
                        <CardContent className="p-4 flex flex-wrap items-center gap-2">
                            <span className="font-mono font-bold text-purple-600 bg-purple-50 px-2 py-1 rounded">
                                {index === 0 ? 'IF' : 'ELSE IF'}
                            </span>
                            
                            <select 
                                className="p-2 border rounded bg-white text-sm"
                                value={rule.property}
                                onChange={(e) => updateRule(rule.id, 'property', e.target.value)}
                            >
                                <option value="Type">Type</option>
                                <option value="Color">Color</option>
                                <option value="Name">Name</option>
                            </select>

                            <span className="text-gray-400 text-sm">is</span>

                            <select 
                                className="p-2 border rounded bg-white text-sm min-w-[100px]"
                                value={rule.value}
                                onChange={(e) => updateRule(rule.id, 'value', e.target.value)}
                            >
                                {rule.property === 'Type' && ['Fruit', 'Veggie', 'Toy'].map(o => <option key={o} value={o}>{o}</option>)}
                                {rule.property === 'Color' && ['Red', 'Green', 'Yellow'].map(o => <option key={o} value={o}>{o}</option>)}
                                {rule.property === 'Name' && ['Apple', 'Banana', 'Broccoli', 'Carrot'].map(o => <option key={o} value={o}>{o}</option>)}
                            </select>

                            <span className="text-gray-400 text-sm"> Send to</span>

                            <select 
                                className="p-2 border rounded bg-white text-sm font-bold text-blue-600"
                                value={rule.destination}
                                onChange={(e) => updateRule(rule.id, 'destination', e.target.value)}
                            >
                                <option value="Bin A">Bin A</option>
                                <option value="Bin B">Bin B</option>
                            </select>

                            {rules.length > 1 && (
                                <button onClick={() => removeRule(rule.id)} className="ml-auto text-red-400 hover:text-red-600">
                                    <X className="w-4 h-4" />
                                </button>
                            )}
                        </CardContent>
                    </Card>
                ))}

                {/* Implicit Else */}
                <div className="p-4 rounded-lg border-2 border-dashed border-slate-300 text-slate-500 text-sm font-mono flex items-center gap-2 bg-slate-50/50">
                    <span className="font-bold text-slate-600">ELSE</span>
                    <span> Send everything else to</span>
                    <span className="font-bold text-slate-700">Bin B</span>
                </div>
            </div>

            {/* Hint Box */}
            <div className="mt-auto pt-6">
                <div className="bg-yellow-50 border border-yellow-200 p-4 rounded-xl flex gap-3">
                    <Lightbulb className="w-6 h-6 text-yellow-600 shrink-0" />
                    <div>
                        <p className="font-bold text-yellow-800 text-sm mb-1">Manager's Note:</p>
                        <p className="text-yellow-700 text-sm font-mono">{currentLevel.hint}</p>
                    </div>
                </div>
            </div>
          </div>

          {/* RIGHT: Visual Factory */}
          <div className="flex-1 bg-white relative flex flex-col">
            
            {/* Log Feed */}
            <div className="absolute top-4 right-4 z-10 w-64">
                {logs.map((log, i) => (
                    <div key={i} className="bg-slate-800 text-white text-xs p-2 rounded mb-1 opacity-90 animate-in slide-in-from-right-2">
                        {log}
                    </div>
                ))}
            </div>

            {/* Conveyor Belt Area */}
            <div className="flex-1 flex flex-col items-center justify-center bg-slate-50 border-b-8 border-slate-200 relative overflow-hidden">
                {/* Belt Track */}
                <div className="w-full h-32 bg-gray-200 flex items-center justify-center relative border-y-4 border-gray-300">
                    {/* Moving track pattern */}
                    <div className="absolute inset-0 opacity-10 bg-[repeating-linear-gradient(45deg,black,black_10px,transparent_10px,transparent_20px)]"></div>
                    
                    {/* The Active Item */}
                    {beltItems.length > 0 && (
                        <div className="w-20 h-20 bg-white rounded-full shadow-xl flex items-center justify-center text-5xl z-10 border-4 border-white animate-bounce">
                            {beltItems[0].icon}
                        </div>
                    )}
                    
                    {beltItems.length === 0 && (
                        <div className="text-gray-400 font-bold uppercase tracking-widest">Belt Empty</div>
                    )}
                </div>

                {/* Queue Preview */}
                <div className="flex gap-2 mt-4 opacity-50">
                    {beltItems.slice(1, 6).map((item, i) => (
                        <div key={i} className="w-10 h-10 bg-gray-100 rounded flex items-center justify-center grayscale">
                            {item.icon}
                        </div>
                    ))}
                </div>
            </div>

            {/* Bins Area */}
            <div className="h-1/3 bg-white flex justify-around items-end p-8 pb-12">
                {/* BIN A */}
                <div className="relative group">
                    <div className="w-40 h-40 bg-blue-100 rounded-b-xl border-4 border-blue-300 border-t-0 shadow-inner flex items-end justify-center pb-4">
                        <span className="text-6xl group-hover:scale-110 transition-transform"></span>
                    </div>
                    <div className="absolute -top-8 left-0 right-0 text-center">
                        <div className="bg-blue-600 text-white px-4 py-1 rounded-full font-bold text-sm shadow-md">
                            Bin A
                        </div>
                        <div className="text-xs text-blue-400 mt-1 font-bold">{currentLevel.bins[0].label}</div>
                    </div>
                </div>

                {/* BIN B */}
                <div className="relative group">
                    <div className="w-40 h-40 bg-slate-100 rounded-b-xl border-4 border-slate-300 border-t-0 shadow-inner flex items-end justify-center pb-4">
                        <span className="text-6xl group-hover:scale-110 transition-transform"></span>
                    </div>
                    <div className="absolute -top-8 left-0 right-0 text-center">
                        <div className="bg-slate-600 text-white px-4 py-1 rounded-full font-bold text-sm shadow-md">
                            Bin B
                        </div>
                        <div className="text-xs text-slate-400 mt-1 font-bold">{currentLevel.bins[1].label}</div>
                    </div>
                </div>
            </div>

          </div>
        </div>
      </div>
    );
  }

  if (gameState === 'won') {
    return (
      <div className="min-h-screen bg-green-50 flex items-center justify-center p-4">
        <Card className="w-full max-w-md bg-white border-green-200 shadow-xl text-center">
          <CardContent className="pt-12 pb-10">
            <div className="w-24 h-24 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-6 animate-bounce">
              <Trophy className="w-12 h-12 text-green-600" />
            </div>
            
            <h2 className="text-4xl font-black text-slate-800 mb-2">Production Perfect!</h2>
            <p className="text-slate-500 mb-8 text-lg">You sorted all items correctly.</p>
            
            <div className="flex gap-4 justify-center">
              <Button onClick={() => {
                  const next = currentLevelId! + 1;
                  if (LEVELS.find(l => l.id === next)) startLevel(next);
                  else setGameState('menu');
              }} className="bg-green-600 hover:bg-green-700 text-white h-12 px-8 text-lg rounded-xl shadow-lg">
                Next Shift
              </Button>
              <Button onClick={() => setGameState('menu')} variant="outline" className="h-12 px-8 text-lg rounded-xl">
                <RotateCcw className="w-4 h-4 mr-2" /> Menu
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return null;
}